h1 {
  margin: 0 1em;
  color: white;
  text-shadow: 2px 2px #2E1114,
}

ul li {
  list-style-type: none;
  margin: .5em 0;
  font-size: 1.5em;
  color: white;
  text-shadow: 2px 2px #64485C;
  width: 50%;
}

li:hover {
  text-decoration: underline;
  cursor: pointer;
}


h2 {
    margin: 1em 0em;
    color: white;
    text-shadow: 2px 2px #64485C;
}

body {
    background-color: #501B1D;
}

.event {
    margin: 1em auto;
    width: 80%;
}

.event-detail {
    width: auto;
    padding: 1em;
    color: white;
    border-radius: 5px;
    text-shadow: 2px 2px #64485C;
    background-color: #ADADAD;
}

.event > ul li {
    margin: 0em;
}

.event > ul li:hover {
    cursor: none;
    text-decoration: none;
}
